:pointer;background:#FFFFFF;border-color:#EB5757}.searchbar__change-guests .number-button__variator--disabled{background:#e0e0e0;pointer-events:none}.searchbar__change-guests .number-button__display{color:#333;width:auto;height:18px;font-weight:bold;font-size:14px;margin:0 8px}.searchbar--active{z-index:1;height:100%;width:100%;position:fixed;overflow:visible;transition:0.3s}.searchbar--active+.stays{margin-top:180px}.searchbar--active .searchbar__location,.searchbar--active .searchbar__guests{width:30%;transition:0.3s}.searchbar--active .searchbar__inputs{-webkit-flex-wrap:wrap;flex-wrap:wrap;position:absolute;top:0;margin-left:200px;-webkit-justify-content:left;justify-content:left;transition:0.3s}.searchbar--active .searchbar__button{color:white;background:#EB5757;width:120px;border-radius:16px;text-align:center;margin-left:40px;transition:0.3s}.searchbar--active .searchbar__button p{font-size:16px;transition:0.3s}.searchbar--active .searchbar__label,.searchbar--active .searchbar__guests-text,.searchbar--active .searchbar__location-text{margin:2px 5px 0 20px}.searchbar--active .searchbar__guests{border-radius:0 16px 16px 0;transition:0.3s}.searchbar--active .searchbar__change-guests{-webkit-animation:list-animation 0.3s ease-out both;animation:list-animation 0.3s ease-out both;margin:45px 0 0 20px}.searchbar--active .searchbar__location-list ul{-webkit-animation:list-animation 0.3s ease-out both;animation:list-animation 0.3s ease-out both}.searchbar--active .searchbar__background{height:400px;width:100%;background:white;position:absolute;top:0px}.searchbar--active .searchbar__filter{height:100%;width:3000%;position:relative;left:-50%;top:297px;z-index:-1;background:rgba(79,79,79,0.4);transition:0.3s}.searchbar--inactive{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;position:relative;width:100%;height:180px;overflow:hidden}.searchbar--inactive .searchbar__inputs{display:-webkit-flex;display:flex;-webkit-flex-